:root {
  background: #4e3f2c;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.movie-list {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.movie-card {
  color: #444;
  cursor: pointer;
  background: #fdf6ee;
  border-radius: 10px;
  width: fit-content;
  margin: 10px;
  padding: 30px;
  font-size: 22px;
  transition: all .5s;
}

.movie-card:hover {
  transform: scale(1.2);
}

.movie-detail {
  gap: 20px;
  align-items: left;
  color: #444;
  background: #fdf6ee;
  border-radius: 10px;
  flex-direction: row;
  width: fit-content;
  margin: 10px;
  padding: 40px;
  font-size: 20px;
  display: flex;
}

.movie-details-list {
  align-items: left;
  flex-direction: column;
  width: fit-content;
  margin: 10px;
  padding: 10px;
  transition: all 1s;
  display: flex;
}

button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #661414;
  border: none;
  border-radius: 10px;
  width: 150px;
  height: 50px;
  margin: 4px 2px;
  padding: 15px 32px;
  font-size: 20px;
  text-decoration: none;
  transition: all .5s;
}

button:hover {
  background-color: #a52121;
}

img {
  filter: contrast(120%);
  border-radius: 10px;
  width: 400px;
  height: auto;
  margin-right: 20px;
}

@media (width <= 1200px) {
  img {
    width: 300px;
    height: auto;
  }
}

@media (width <= 900px) {
  img {
    width: 300px;
    height: auto;
  }

  .movie-detail {
    flex-direction: column;
  }
}
